"use client";

import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  Tooltip,
  CartesianGrid,
  ResponsiveContainer,
  Legend
} from "recharts";

export default function ForecastChart({ historyData, forecastData, retrainModel }) {

  // ---------- ACTUAL DATA ----------
  const safeHistory = Array.isArray(historyData)
    ? historyData.map(d => ({
        year: d.year,
        actual: d.totalIncome ?? 0,
      }))
    : [];

  // ---------- FORECAST DATA (PROPHET) ----------
  const safeForecast = Array.isArray(forecastData)
  ? forecastData.map(f => ({
      year: f.year,
      forecast: f.value ?? 0,
    }))
  : [];


  const combinedData = [...safeHistory, ...safeForecast];

  return (
    <div className="bg-white shadow rounded-lg p-4 mt-6">
      <h3 className="text-lg font-semibold mb-2">

      </h3>

      <ResponsiveContainer width="100%" height={350}>
        <LineChart data={combinedData}>
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="year" />
          <YAxis tickFormatter={(v) => `₱${(v / 1_000_000).toFixed(1)}M`} />
          <Tooltip formatter={(v) => `₱${v.toLocaleString()}`} />
          <Legend />

          <Line
            type="monotone"
            dataKey="actual"
            name="Actual Budget"
            stroke="#4F46E5"
            strokeWidth={3}
            dot={{ r: 4 }}
            isAnimationActive={false}
            connectNulls
          />

          <Line
            type="monotone"
            dataKey="forecast"
            name="Forecasted Budget "
            stroke="#10B981"
            strokeDasharray="6 6"
            strokeWidth={3}
            dot={{ r: 4 }}
            isAnimationActive={false}
            connectNulls
          />
        </LineChart>
      </ResponsiveContainer>

    
    </div>
  );
}
