"use client"

import { useState, useEffect } from "react"
import { supabase } from "../../../lib/supabaseClient"
import { AlertCircle, TrendingUp, RefreshCw, Upload, BarChart3 } from "lucide-react"

// Hooks
import useBudgetData from "./lib/useBudgetData"
import useBreakdownData from "./lib/useBreakdownData"

// Components
import BudgetUploader from "./components/budgetUploader"
import SummaryCards from "./components/summaryCards"
import IncomeTrendChart from "./components/incomeTrendChart"
import BreakdownPieSection from "./components/breakdownPieSection"
import ForecastChart from "./components/forecastChart"
import ForecastSummary from "./components/forecastSummary"

export default function TransactionPage() {
  const [session, setSession] = useState(null)
  const [selectedYear, setSelectedYear] = useState(null)
  const [forecastData, setForecastData] = useState([])
  const [isRetraining, setIsRetraining] = useState(false)
  const [error, setError] = useState(null)

  // Custom hooks
  const { budgetData, loading, fetchBudgetData } = useBudgetData()
  const { breakdownData, fetchBreakdownData } = useBreakdownData()

  useEffect(() => {
    if (budgetData.length > 0) {
      loadForecast();
    }
  }, [budgetData]);

  // -------------------------------------------------------
  // LOAD FORECAST FROM PYTHON BACKEND
  // -------------------------------------------------------
  async function loadForecast() {
    try {
      setError(null)
      const response = await fetch("/api/forecast", { cache: "no-store" });
      
      if (!response.ok) {
        throw new Error('Failed to fetch forecast data')
      }
      
      const data = await response.json();

      // Find last actual year
      const lastActualYear =
        budgetData.length > 0
          ? Math.max(...budgetData.map(d => d.year))
          : 0;

      // Keep only FUTURE forecast years
      const formatted = Array.isArray(data)
        ? data
            .filter(d => d.year > lastActualYear)
            .map(d => ({
              year: d.year,
              value: Number(d.yhat_upper),
              lower: Number(d.yhat_lower),
              upper: Number(d.yhat_upper),
            }))
        : [];

      console.log("Filtered future forecast:", formatted);
      setForecastData(formatted);

    } catch (err) {
      console.error("Forecast API error:", err);
      setError("Failed to load forecast data. Please try again.")
    }
  }


  // -------------------------------------------------------
  // AUTH CHECK
  // -------------------------------------------------------
  useEffect(() => {
    supabase.auth.getSession().then(({ data: { session } }) => {
      setSession(session)
      if (session) {
        fetchBudgetData()
        fetchBreakdownData()
      }
    })

    const { data: listener } = supabase.auth.onAuthStateChange(
      (_event, session) => {
        setSession(session)
        if (session) {
          fetchBudgetData()
          fetchBreakdownData()
        }
      }
    )

    return () => listener.subscription.unsubscribe()
  }, [])

  


  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header Section */}
      <div className="bg-white shadow-sm border-b border-gray-200">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-3">
              <div className="flex items-center justify-center h-10 w-10 rounded-lg bg-blue-600">
                <BarChart3 className="h-6 w-6 text-white" />
              </div>
              <div>
                <h1 className="text-2xl font-bold text-gray-900">Barangay Budget Dashboard</h1>
                <p className="text-sm text-gray-600"> Forecast your future budget </p>
              </div>
            </div>
            {budgetData.length > 0 && (
              <div className="flex items-center space-x-2 text-sm text-gray-600">

              </div>
            )}
          </div>
        </div>
      </div>

      {/* Main Content */}
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Error Message */}
        {error && (
          <div className="mb-6 bg-red-50 border border-red-200 rounded-lg p-4">
            <div className="flex items-center">
              <AlertCircle className="h-5 w-5 text-red-400 mr-3" />
              <div className="text-sm text-red-800">{error}</div>
            </div>
          </div>
        )}

        

          {/* Summary Cards */}
          {budgetData.length > 0 && (
            <div className="bg-white rounded-lg shadow-sm border border-gray-200">
              <div className="px-6 py-4 border-b border-gray-200">
                <h3 className="text-lg font-medium text-gray-900">Budget Overview</h3>
              </div>
              <div className="p-6">
                <SummaryCards data={budgetData} />
              </div>
            </div>
          )}


          {/* Forecast Section */}
          {forecastData.length > 0 && (
            <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
              {/* Forecast Summary */}
              <div className="lg:col-span-1">
                <div className="bg-white rounded-lg shadow-sm border border-gray-200">
                  <div className="px-6 py-4 border-b border-gray-200">
                    <h3 className="text-lg font-medium text-gray-900">Forecast Summary</h3>
                    <p className="mt-1 text-sm text-gray-600">Predicted budget performance</p>
                  </div>
                  <div className="p-6">
                    <ForecastSummary data={forecastData} />
                  </div>
                </div>
              </div>

              {/* Forecast Chart */}
              <div className="lg:col-span-2">
                <div className="bg-white rounded-lg shadow-sm border border-gray-200">
                  <div className="px-6 py-4 border-b border-gray-200 flex items-center justify-between">
                    <div>
                      <h3 className="text-lg font-medium text-gray-900"> Forecasted Budget(Next 3 Years)</h3>
                    </div>
                  
                  </div>
                  <div className="p-6">
                    <ForecastChart
                      historyData={budgetData}
                      forecastData={forecastData}
                    />
                  </div>
                </div>
              </div>
            </div>
          )}

          {/* Empty State */}
          {budgetData.length === 0 && !loading && (
            <div className="text-center py-12">
              <div className="mx-auto flex items-center justify-center h-12 w-12 rounded-full bg-gray-100 mb-4">
                <BarChart3 className="h-6 w-6 text-gray-400" />
              </div>
              <h3 className="text-lg font-medium text-gray-900 mb-2">No Budget Data Found</h3>
              <p className="text-sm text-gray-600 mb-6">Get started by uploading your first budget file</p>
            </div>
          )}
        </div>
      </div>
   
  )
}